:root {
  --default-font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Ubuntu, 'Helvetica Neue', Helvetica, Arial, 'PingFang SC',
    'Hiragino Sans GB', 'Microsoft Yahei UI', 'Microsoft Yahei',
    'Source Han Sans CN', sans-serif;
}

.mainContainer {
  overflow: hidden;
}

.mainContainer,
.mainContainer * {
  box-sizing: border-box;
}

input,
select,
textarea,
button {
  outline: 0;
}

.mainContainer {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: nowrap;
  position: relative;
  width: 1440px;
  margin: 0 auto;
  padding: 64px 80px 64px 80px;
  background: #f3f6f7;
}
@media screen and (max-width: 1279px) {
  .mainContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: nowrap;
    gap: 24px;
    position: relative;
    width: 412px;
    margin: 0 auto;
    padding: 48px 20px 48px 20px;
    background: #f3f6f7;
  }
}

.frame {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: nowrap;
  flex-shrink: 0;
  gap: 32px;
  position: relative;
  width: 560px;
}
@media screen and (max-width: 1279px) {
  .frame {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: nowrap;
    flex-shrink: 0;
    gap: 24px;
    position: relative;
    width: 372px;
    z-index: 3;
  }
}

.frame1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-self: stretch;
  flex-wrap: nowrap;
  flex-shrink: 0;
  gap: 16px;
  position: relative;
  min-width: 0;
  z-index: 1;
}
@media screen and (max-width: 1279px) {
  .frame1 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-self: stretch;
    flex-wrap: nowrap;
    flex-shrink: 0;
    gap: 8px;
    position: relative;
    min-width: 0;
    z-index: 4;
  }
}
.dressYourNest {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  align-self: stretch;
  flex-shrink: 0;
  position: relative;
  width: 560px;
  height: 128px;
  color: #0c1012;
  font-family: Adelle, var(--default-font-family);
  font-size: 48px;
  font-weight: 600;
  line-height: 64px;
  text-align: left;
  z-index: 2;
}
@media screen and (max-width: 1279px) {
  .dressYourNest {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    align-self: stretch;
    flex-shrink: 0;
    position: relative;
    width: 372px;
    height: 80px;
    color: #0c1012;
    font-family: Adelle, var(--default-font-family);
    font-size: 32px;
    font-weight: 600;
    line-height: 40px;
    text-align: center;
    z-index: 5;
  }
}

.exclusiveDeals {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  align-self: stretch;
  flex-shrink: 0;
  position: relative;
  width: 560px;
  height: 48px;
  color: #0c1012;
  font-family: HvDTrial Brandon Text, var(--default-font-family);
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
  text-overflow: initial;
  z-index: 3;
  overflow: hidden;
}
@media screen and (max-width: 1279px) {
  .exclusiveDeals {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    align-self: stretch;
    flex-shrink: 0;
    position: relative;
    width: 372px;
    height: 48px;
    color: #0c1012;
    font-family: HvDTrial Brandon Text, var(--default-font-family);
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    z-index: 6;
  }
}
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  flex-shrink: 0;
  gap: 12px;
  position: relative;
  width: 164px;
  padding: 16px 48px 16px 48px;
  cursor: pointer;
  background: #3b525b;
  border: none;
  z-index: 4;
  border-radius: 4px;
}
.label {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-shrink: 0;
  flex-basis: auto;
  position: relative;
  width: 68px;
  height: 18px;
  color: #fbfcfc;
  font-family: HvDTrial Brandon Text, var(--default-font-family);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  text-align: center;
  white-space: nowrap;
  z-index: 5;
}
.frame2 {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: stretch;
  flex-wrap: nowrap;
  flex-shrink: 0;
  gap: 16px;
  position: relative;
  width: 630px;
  z-index: 6;
}
@media screen and (max-width: 1279px) {
  .frame2 {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    align-self: stretch;
    flex-wrap: nowrap;
    flex-shrink: 0;
    gap: 16px;
    position: relative;
    min-width: 0;
    order: -1;
  }
}
.rectangle {
  flex-shrink: 0;
  position: relative;
  width: 354px;
  height: 380px;
  background: url(../../../images/rectangle-22.jpg) no-repeat center;
  background-size: cover;
  z-index: 7;
}
@media screen and (max-width: 1279px) {
  .rectangle {
    flex-shrink: 0;
    position: relative;
    width: 210px;
    height: 200px;
    background-size: cover;
    z-index: 1;
  }
}

.rectangle3 {
  flex-shrink: 0;
  position: relative;
  width: 260px;
  height: 300px;
  background: url(../../../images/rectangle-21.jpg) no-repeat center;
  background-size: cover;
  z-index: 8;
}
@media screen and (max-width: 1279px) {
  .rectangle3 {
    flex-shrink: 0;
    position: relative;
    width: 150px;
    height: 160px;
    background-size: cover;
    z-index: 2;
  }
}
